var rank = require('./constants/statusRank.json')

const numberToRank = () => {

  const getAdvRank = (stats) => {
    // 0 STR 
    // 1 END
    // 2 DEX
    // 3 AGI
    // 4 MAG
    const output = [-1, -1, -1, -1, -1]
    const urlOutput = ["", "", "", "", ""]
    const keys = Object.keys(rank.adventurers);
    keys.forEach((key, index) => {
      for (let i = 0; i < rank.adventurers[key].length - 1; i++){
        if(stats[index] >= rank.adventurers[key][i] && stats[index] < rank.adventurers[key][i + 1]){
          output[index] = i + 1
          break
        }
      }
      // Se for maior que o ultimo valor, é SSS
      if(stats[index] >= rank.adventurers[key].slice(-1)){
        output[index] = rank.adventurers[key].length
      }
    })
    // console.log(stats)
    // console.log(output)

    output.forEach((out, index) => {
      urlOutput[index] = "evaluation_" + out
    })

    return urlOutput
  }

  const getAstRank = (stats) => {
    // 0 STR 
    // 1 END
    // 2 DEX
    // 3 AGI
    // 4 MAG
    const output = [-1, -1, -1, -1, -1]
    const urlOutput = ["", "", "", "", ""]
    const keys = Object.keys(rank.assists);
    keys.forEach((key, index) => {
      for (let i = 0; i < rank.assists[key].length - 1; i++){
        if(stats[index] >= rank.assists[key][i] && stats[index] < rank.assists[key][i + 1]){
          output[index] = i + 1
          break
        }
      }
      // Se for maior que o ultimo valor, é SSS
      if(stats[index] >= rank.assists[key].slice(-1)){
        output[index] = rank.assists[key].length
      }
    })

    output.forEach((out, index) => {
      urlOutput[index] = "evaluation_" + out
    })

    return urlOutput
  }

  return { getAdvRank, getAstRank }
}

export default numberToRank